      width: 255px;
      height: 2px;
      background-color: #FFFFFF;
      position: absolute;
      top: -23px;
      left: -23px; }
    .main__caption::after {
      content: "";
      display: block;
      width: 9px;
      height: 9px;
      border-radius: 50%;
      background-color: #E9B730;
      position: absolute;
      top: 13px;
